<template>
  <div style="text-align: center;">
    <div id="box">
      <p id="name">장비관리</p>
    </div>
    <div>
      <br /><br />

      <div>
        <select
          name="count"
          data-title="병원명"
          style="  width: 80px; 
                font-family: inherit; 
                border: 1px solid #999;"
        >
          <option value="0">병원명</option>
          <option value="1">내용</option>
          <option value="2">작성자</option>
          <option value="3">작성일</option>
        </select>
        <input
          type="text"
          style=" width: 60%; padding: 12px 20px; margin: 8px 0; box-sizing: border-box; border: 3px solid #555;"
          v-model="searchTerm"
          placeholder="병원명을 입력하세요"
        />
      </div>

      <v-btn
        class="ma-2"
        :loading="loading2"
        :disabled="loading2"
        color="success"
        @click="submitALL"
      >
        데이터 전체보기
      </v-btn>
      <v-btn
        class="ma-2"
        :loading="loading2"
        :disabled="loading2"
        color="success"
        @click="submit"
      >
        검색
      </v-btn>
      <div style="width:100%; height:800px; overflow:auto">
        <table style=" border: 1px solid black;  width: 100%;">
          <thead>
            <tr>
              <th style=" border: 1px solid black;" scope="col">NO.</th>
              <th style=" border: 1px solid black;" scope="col">병원명</th>
              <th style=" border: 1px solid black;" scope="col">장비명</th>
              <th style=" border: 1px solid black;" scope="col">제조사</th>
              <th style=" border: 1px solid black;" scope="col">serial-no</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in items" :key="item.HOSPITAL_KEY">
              <td style=" border: 1px solid black;">{{ index + 1 }}</td>
              <td style=" border: 1px solid black;">
                {{ item.HOSPITAL_NAME }}
              </td>
              <td style=" border: 1px solid black;">
                {{ item.EQUIPMENT_NAME }}
              </td>
              <td style=" border: 1px solid black;">{{ item.COMPANY }}</td>
              <td style=" border: 1px solid black;">{{ item.SERIAL_NO }}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      items: [],
      searchTerm: "",
    };
  },
  methods: {
    submit: function() {
      var params = new URLSearchParams();
      params.append("searchTerm", this.searchTerm);

      axios
        .post("http://192.1.1.30:3000/ind/equipmentManage", params)
        .then((res) => {
          console.log(res.data);
          // //첫번째 배열 가져오기
          // const InfoArray = Object.values(res.data[0])
          this.items = res.data;
        })
        .catch(function(err) {
          console.log(err);
        });
    },

    submitALL: function() {
      //데이터 전체 보기
      axios
        .get("http://192.1.1.30:3000/ind/equipmentManageALL")
        .then((res) => {
          console.log(res.data);
          this.items = res.data;
        })
        .catch(function(err) {
          console.log(err);
        });
    },
  },
};
</script>

<style scoped>
#top {
  width: 294px;
  height: 40.2px;
}

#text1 {
  width: 147px;
  height: 21px;
  color: rgba(0, 0, 0, 0.74);
}

#box {
  height: 30px;
  background-color: #99d0e2;
}

#name {
  width: 414px;
  height: 896px;

  color: #ffffff;
}

#searchBTN {
  width: 47px;
  height: 34px;
  margin: 26px 38px 60px 0;
  border-radius: 11px;
}

#addBTN {
  width: 47px;
  height: 34px;
  margin: 26px 38px 60px 0;
  border-radius: 11px;
}

#searchBox {
  width: 215px;
  height: 30px;
  margin: 30px 11px 45px 38px;
  padding: 7px 115px 6px 112px;
  border-radius: 9px;
  border: solid 1px #707070;
  background-color: #ffffff;
}
#placeHolder {
  width: 53px;
  height: 17px;
  font-size: 15px;
  font-weight: normal;
  font-stretch: normal;
  line-height: 1.13;
  letter-spacing: normal;
  text-align: left;
  color: rgba(77, 58, 58, 0.75);
}

#th {
  color: #168;
  background: #f0f6f9;
  text-align: center;
}

#tr {
  text-align: center;
}
</style>
